/* Buttons */
.button, .btn-border, .btn-default {
  cursor: pointer;
  padding: 3px 15px;
  font-size: 16px;
  text-decoration: none;
  line-height: 36px;
  border: 0;
  border-radius: 4px;
  font-weight: bold;
  margin: 2px 5px; }

.btn-border {
  color: #9baec8;
  background-color: transparent;
  border: 1px solid #9baec8; }
  .btn-border:hover {
    border: 1px solid rgba(155, 174, 200, 0.6);
    color: rgba(155, 174, 200, 0.6);
    -webkit-transition: all 0.6s;
    -o-transition: all 0.6s;
    transition: all 0.6s; }

.btn-default {
  color: #1f232b;
  background-color: #9baec8; }
  .btn-default:hover {
    color: rgba(31, 35, 43, 0.6);
    background-color: rgba(155, 174, 200, 0.6);
    -webkit-transition: all 0.6s;
    -o-transition: all 0.6s;
    transition: all 0.6s; }

.invite .server-link {
  margin-top: 10px;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  width: 100%; }
  .invite .server-link input {
    margin: 0; }
  .invite .server-link button {
    margin: 0;
    width: 50%; }

.invite .expiry {
  font-size: 12px; }

.invite .footer {
  font-size: 14px;
  margin-top: 20px; }
  .invite .footer input {
    display: -ms-flexbox;
    display: flex;
    width: auto;
    height: 16px;
    margin: 0; }



